import { Backward } from "./Backward";
import { Forward } from "./Forward";
export class Viewer {
    constructor(history, renderer) {
        this.sequencesNow = null;
        this._active = false;
        this._waiting = false;
        this.history = history;
        this.renderer = renderer;
        this.now = -1;
    }
    clear() {
        this.now = -1;
        this.sequencesNow = [];
        this.renderer.clear();
    }
    createImageDataURI() {
        return this.renderer.createImageDataURI();
    }
    show(target) {
        this.show(target);
    }
    hide(target) {
        this.hide(target);
    }
    start() {
        this._active = true;
        if (this._waiting) {
            return;
        }
        this._waiting = true;
        try {
            this.updateView();
        }
        catch (e) {
            console.warn(e);
        }
        this.history.awaitUpdate(() => {
            this._waiting = false;
            if (this._active) {
                this.start();
            }
        });
    }
    stop() {
        this._active = false;
    }
    getPixelColor(x, y, layerIndex) {
        return this.renderer.getPixelColor(x, y, layerIndex);
    }
    refresh() {
        this.now = -1;
        this.updateView();
    }
    updateView() {
        let number1 = this.history.getNowHistoryNumber();
        if (this.now === number1) {
            return;
        }
        if (!this.history.isAvailable(this.now)) {
            this.now = -1;
            this.sequencesNow = [];
            this.renderer.clear();
        }
        // 過去へ戻る（Undoなど）
        if (this.now > number1) {
            this.sequencesNow = Backward.updateView(this.renderer, this.sequencesNow, this.history.getLayers(number1), this.history.getMoments(-1, number1), this.history.getMoments(number1 + 1, this.now));
            this.now = number1;
            return;
        }
        // 進む
        this.sequencesNow = Forward.updateView(this.renderer, this.sequencesNow, this.history.getLayers(number1), this.history.getMoments(-1, this.now), this.history.getMoments(this.now + 1, number1));
        this.now = number1;
    }
}
export default Viewer;



