import { EVENT_CHANGE_PROPERTIES, EVENT_CHANGE_MODE, EVENT_CHANGE_CURRENT_LAYER, EVENT_UPDATE } from "./EditorEventListeners";
export class EditorEventDispatchers {
    constructor(eventEmitter) {
        this._changeProperties = new EventDispatcher(eventEmitter, EVENT_CHANGE_PROPERTIES);
        this._changeMode = new EventDispatcher(eventEmitter, EVENT_CHANGE_MODE);
        this._changeCurrentLayer = new EventDispatcher(eventEmitter, EVENT_CHANGE_CURRENT_LAYER);
        this._update = new EventDispatcher(eventEmitter, EVENT_UPDATE);
    }
    get changeProperties() {
        return this._changeProperties;
    }
    get changeMode() {
        return this._changeMode;
    }
    get changeCurrentLayer() {
        return this._changeCurrentLayer;
    }
    get update() {
        return this._update;
    }
}
export class EventDispatcher {
    constructor(emitter3, eventName) {
        this.eventName = eventName;
        this.emitter3 = emitter3;
    }
    dispatch(val) {
        this.emitter3.emit(this.eventName, val);
    }
}



