import * as React from "react";
import * as styles from "./MenuBarStyle.css";
import { RedoButton } from "./redo/RedoButton";
import { UndoButton } from "./undo/UndoButton";
import { SaveButton } from "./save/SaveButton";
import { DownloadButton } from "./download/DownloadButton";
export class MenuBar extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (React.createElement("div", {className: styles.container}, 
            (() => {
                if (this.props.canDownload) {
                    return (React.createElement(DownloadButton, {action: () => { this.props.download(); }, disabled: false}));
                }
            })(), 
            React.createElement(RedoButton, {action: () => { this.props.redo(); }, disabled: !this.props.canRedo}), 
            React.createElement(UndoButton, {action: () => { this.props.undo(); }, disabled: !this.props.canUndo}), 
            (() => {
                if (this.props.canSave) {
                    return (React.createElement(SaveButton, {action: () => { this.props.save(); }, disabled: false}));
                }
            })()));
    }
}



