import { AbstractModeStroke } from "./AbstractModeStroke";
export class ModeStroke extends AbstractModeStroke {
    constructor(viewer, tran, prop) {
        super(viewer, tran, prop);
        this.prop = prop;
    }
    setProperty(tran) {
        tran.setStrokeColor(`rgba(${this.prop.color.r},${this.prop.color.g},${this.prop.color.b},${this.prop.alpha})`);
        tran.setStrokeStyle(this.prop.thickness);
    }
    setCommitProperty(tran) {
    }
}



