import RenderEditor from "./RenderEditor";
import History from "@s2study/draw-history/lib";
const history = History.createInstance();
const s2study = window.s2study ? window.s2study : {};
if (!window.s2study) {
    window.s2study = s2study;
}
s2study.render = (saveJSON = null, width, height, disableDownload = true, id = "root") => {
    RenderEditor(history, width, height, id, saveJSON, disableDownload);
};



