import { AbstractLayerTransaction } from "./AbstractLayerTransaction";
import { TransformCalculator } from "./TransformCalculator";
export class Text extends AbstractLayerTransaction {
    constructor(session, history, layerId, editLayerId, transformMap) {
        super(session, history, layerId, editLayerId, transformMap);
    }
    setFill(color) {
        this.fill = {
            color: color
        };
        return this;
    }
    setFillLinerGradient(x0, y0, x1, y1, colorStops) {
        this.init();
        let transform = this.getTransform(this.layerId);
        let invert = TransformCalculator.invert(transform);
        let point1 = TransformCalculator.transform(invert, x0, y0);
        let point2 = TransformCalculator.transform(invert, x1, y1);
        this.fill = {
            linerGradient: {
                x0: point1.x,
                y0: point1.y,
                x1: point2.x,
                y1: point2.y,
                colorStops: colorStops
            }
        };
        return this;
    }
    setFillRadialGradient(x0, y0, r0, x1, y1, r1, colorStops) {
        this.init();
        let transform = this.getTransform(this.layerId);
        let invert = TransformCalculator.invert(transform);
        let point1 = TransformCalculator.transform(invert, x0, y0);
        let point2 = TransformCalculator.transform(invert, x1, y1);
        this.fill = {
            radialGradient: {
                x0: point1.x,
                y0: point1.y,
                r0: r0,
                x1: point2.x,
                y1: point2.y,
                r1: r1,
                colorStops: colorStops
            }
        };
        return this;
    }
    setStrokeColor(color) {
        this.stroke = {
            fillStyle: {
                color: color
            }
        };
        return this;
    }
    push(text) {
        this.init();
        this.text = this.text == null ? text : this.text + text;
        return this;
    }
    setBaseline(baseline) {
        this.baseline = baseline;
        return this;
    }
    setAlign(align) {
        this.align = align;
        return this;
    }
    setPosition(x, y) {
        this.init();
        let transform = this.getTransform(this.layerId);
        let invert = TransformCalculator.invert(transform);
        let point1 = TransformCalculator.transform(invert, x, y);
        this.x = point1.x;
        this.y = point1.y;
        return this;
    }
    setFontFamily(fontFamily) {
        this.fontFamily = fontFamily;
        return this;
    }
    setSize(size) {
        this.size = size;
        return this;
    }
    setWeight(weight) {
        this.weight = weight;
        return this;
    }
    setStyle(style) {
        this.style = style;
        return this;
    }
    setCompositeOperation(compositeOperation) {
        this.compositeOperation = compositeOperation;
        return this;
    }
    flush() {
        this.init();
        this.doUpdate(this.getEditBuilder());
    }
    beforeCommit(duration) {
        this.doUpdate(this.getLayerBuilder());
        super.beforeCommit(duration);
    }
    afterCancel() {
        this.x = undefined;
        this.y = undefined;
        this.fill = undefined;
        this.stroke = undefined;
        this.align = undefined;
        this.baseline = undefined;
        this.text = undefined;
        this.fontFamily = undefined;
        this.size = undefined;
        this.weight = undefined;
        this.style = undefined;
        this.compositeOperation = undefined;
    }
    doUpdate(layerBuilder) {
        if (this.text == null || this.text.length === 0) {
            return;
        }
        layerBuilder.addDraw({
            compositeOperation: this.compositeOperation,
            text: {
                x: this.x,
                y: this.y,
                fill: this.fill,
                stroke: this.stroke,
                align: this.align,
                baseline: this.baseline,
                text: this.text,
                fontFamily: this.fontFamily,
                size: this.size,
                weight: this.weight,
                style: this.style
            }
        })
            .commit().commit();
    }
}



