import { AbstractLayerTransaction } from "./AbstractLayerTransaction";
import { TransformCalculator } from "./TransformCalculator";
export class Clip extends AbstractLayerTransaction {
    constructor(session, history, layerId, editLayerId, transformMap) {
        super(session, history, layerId, editLayerId, transformMap);
        this.path = [];
        this.savedPath = [];
    }
    moveTo(x, y) {
        this.init();
        let transform = this.getTransform(this.layerId);
        let invert = TransformCalculator.invert(transform);
        let point = TransformCalculator.transform(invert, x, y);
        this.path.push({
            type: 0,
            x: point.x,
            y: point.y
        });
        return this;
    }
    arcTo(x1, y1, x2, y2, radius) {
        this.init();
        let transform = this.getTransform(this.layerId);
        let invert = TransformCalculator.invert(transform);
        let point1 = TransformCalculator.transform(invert, x1, y1);
        let point2 = TransformCalculator.transform(invert, x2, y2);
        this.path.push({
            type: 1,
            x1: point1.x,
            y1: point1.y,
            x2: point2.x,
            y2: point2.y,
            radius: radius
        });
        return this;
    }
    quadraticCurveTo(cpx, cpy, x, y) {
        this.init();
        let transform = this.getTransform(this.layerId);
        let invert = TransformCalculator.invert(transform);
        let point1 = TransformCalculator.transform(invert, cpx, cpy);
        let point2 = TransformCalculator.transform(invert, x, y);
        this.path.push({
            type: 2,
            cpx: point1.x,
            cpy: point1.y,
            x: point2.x,
            y: point2.y
        });
        return this;
    }
    lineTo(x, y) {
        this.init();
        let transform = this.getTransform(this.layerId);
        let invert = TransformCalculator.invert(transform);
        let point1 = TransformCalculator.transform(invert, x, y);
        this.path.push({
            type: 3,
            x: point1.x,
            y: point1.y
        });
        return this;
    }
    bezierCurveTo(cpx1, cpy1, cpx2, cpy2, x, y) {
        this.init();
        let transform = this.getTransform(this.layerId);
        let invert = TransformCalculator.invert(transform);
        let point1 = TransformCalculator.transform(invert, cpx1, cpy1);
        let point2 = TransformCalculator.transform(invert, cpx2, cpy2);
        let point3 = TransformCalculator.transform(invert, x, y);
        this.path.push({
            type: 4,
            cpx1: point1.x,
            cpy1: point1.y,
            cpx2: point2.x,
            cpy2: point2.y,
            x: point3.x,
            y: point3.y
        });
        return this;
    }
    flush() {
        this.init();
        this.doUpdate(this.getEditBuilder().setTransForm(this.getTransform(this.layerId)), this.path);
    }
    restoreSavePoint() {
        super.restoreSavePoint();
        this.path = [];
    }
    setSavePoint() {
        super.setSavePoint();
        Array.prototype.push.apply(this.savedPath, this.path);
        this.path = [];
    }
    beforeCommit(duration) {
        Array.prototype.push.apply(this.savedPath, this.path);
        this.doUpdate(this.getLayerBuilder(), this.savedPath);
        this.savedPath = [];
        this.path = [];
        super.setSavePoint();
        super.beforeCommit(duration);
    }
    afterCancel() {
        this.savedPath = [];
        this.path = [];
        super.setSavePoint();
    }
    doUpdate(layerBuilder, path1) {
        if (path1 == null || path1.length === 0) {
            return;
        }
        layerBuilder.setClip({
            path: path1
        })
            .commit().commit();
    }
}



