import * as React from "react";
import * as ReactDOM from "react-dom";
import { EditorRoot } from "./EditorRoot";
export default function (history, width, height, id = "root", saveJSON = null, disableDownload = true) {
    ReactDOM.render(React.createElement(EditorRoot, {history: history, save: saveJSON, disableDownload: disableDownload, canvasWidth: width, canvasHeight: height}), document.getElementById(id));
}



