import { LayerMoment } from "./LayerMoment";
export class LayerMomentBuilder {
    constructor(layerId, momentBuilder) {
        this.layerId = layerId;
        this.momentBuilder = momentBuilder;
    }
    setTransForm(transform) {
        this.transform = transform;
        return this;
    }
    setClip(clip) {
        this.clip = clip;
        return this;
    }
    addDraw(draw) {
        if (!this.draws) {
            this.draws = [];
        }
        this.draws.push(draw);
        return this;
    }
    addDraws(draws) {
        if (!this.draws) {
            this.draws = [];
        }
        for (let draw of draws) {
            this.draws.push(draw);
        }
        return this;
    }
    commit() {
        if (!this.clip && !this.transform && !this.draws) {
            return this.momentBuilder;
        }
        if (!this.momentBuilder.layerMap) {
            this.momentBuilder.layerMap = {};
        }
        this.momentBuilder.layerMap[this.layerId] = new LayerMoment(this.layerId, {
            transform: this.transform,
            clip: this.clip,
            draws: this.draws
        });
        return this.momentBuilder;
    }
}



