import { PathDrawer } from "./PathDrawer";
export class AbstractModeFill {
    constructor(viewer, tran, prop) {
        this.viewer = viewer;
        this.tran = tran;
        this.tran.setSavePoint();
        this.pathDrawer = new PathDrawer(tran, prop);
        this.lPointX = -100;
        this.lPointY = -100;
    }
    touchStart(x, y) {
        this.touchMove(x, y);
    }
    touchMove(x, y) {
        if (!this.tran.isAlive()) {
            return;
        }
        let latest = this.time;
        this.time = new Date().getTime();
        if ((this.time - latest) >= 50) {
            // this.tran.restoreSavePoint();
            this.doFill(x, y);
            return;
        }
        let x1 = x - this.lPointX;
        let y1 = y - this.lPointY;
        let d = Math.sqrt(x1 * x1 + y1 * y1);
        if (d < 5) {
            this.wPointX = x;
            this.wPointY = y;
            this.setWait();
            return;
        }
        this.doFill(x, y);
    }
    touchEnd(x, y) {
        this.touchMove(x, y);
    }
    setWait() {
        if (this.waiting) {
            return;
        }
        this.waiting = true;
        setTimeout(() => {
            if (!this.waiting) {
                return;
            }
            this.waiting = false;
            if (!this.tran.isAlive()) {
                return;
            }
            let now = new Date().getTime();
            if (now - this.time < 100) {
                this.setWait();
                return;
            }
            this.doFill(this.wPointX, this.wPointY);
        }, 100);
    }
    doFill(x, y) {
        this.waiting = false;
        this.lPointX = x;
        this.lPointY = y;
        this.viewer.stop();
        try {
            this.tran.restoreSavePoint();
            this.setProperty(this.tran);
            if (x < 0 && y < 0) {
                this.pathDrawer.doPlot(true);
                return;
            }
            this.pathDrawer.push(this.lPointX, this.lPointY).doPlot(true);
        }
        finally {
            this.tran.flush();
            this.viewer.start();
        }
    }
    setText(text) {
        // 処理なし。
    }
    backward() {
        this.pathDrawer.pop();
        let point = this.pathDrawer.pop();
        if (point != null) {
            point = {
                x: -100,
                y: -100
            };
        }
        this.doFill(point.x, point.y);
    }
}



