import { PathUtil } from "./PathUtil";
export class ClipUtil {
    static setClip(context, transform, clip) {
        if (!clip) {
            return;
        }
        transform.setTransform(context, clip.transform);
        PathUtil.drawPathArray(context, clip.path);
        context.clip();
    }
}



