import { AbstractTransaction } from "./AbstractTransaction";
export class AbstractLayerTransaction extends AbstractTransaction {
    constructor(session, history, layerId, editLayerId, transformMap) {
        super(session, history);
        this.layerId = layerId;
        this.editLayerId = editLayerId;
        this.initialized = false;
        this.transformMap = transformMap;
    }
    init() {
        if (!this.initialized) {
            this.setup(true);
            this.initialized = true;
            this.transformMap.updateMap(this.history);
            return;
        }
        this.session.setHistoryNumberNow(this.reservedPoint);
        this.transformMap.updateMap(this.history);
    }
    setup(editLayer) {
        if (editLayer) {
            this.setupEditLayer();
        }
        // this.reservedPoint = this.history.getNowHistoryNumber();
        super.setSavePoint();
    }
    /**
     * 編集用レイヤーをセットアップする。
     */
    setupEditLayer() {
        let layers = this.history.getLayers();
        let i = 0 | 0;
        let result = [];
        let item;
        let addIndex = -1;
        if (layers == null) {
            layers = [];
        }
        while (i < layers.length) {
            item = layers[i];
            if (item === this.editLayerId) {
                // 並び替え不要
                if (i === addIndex) {
                    this.reservedPoint = this.history.getNowHistoryNumber();
                    return;
                }
                i = (i + 1) | 0;
                continue;
            }
            result.push(item);
            i = (i + 1) | 0;
            if (item === this.layerId) {
                result.push(this.editLayerId);
                addIndex = i;
            }
        }
        if (result.length === 0) {
            result.push(this.editLayerId);
        }
        this.session.addMoment().setSequence(result).commit();
        this.reservedPoint = this.history.getNowHistoryNumber();
    }
    setSavePoint() {
        super.setSavePoint();
        this.reservedPoint = this.history.getNowHistoryNumber();
    }
    restoreSavePoint() {
        super.restoreSavePoint();
        this.reservedPoint = this.history.getNowHistoryNumber();
    }
    getTransform(layerId) {
        return this.transformMap.getTransForm(layerId);
    }
    beforeCommit(duration) {
    }
    afterCommit(duration) {
        this.setup(duration);
    }
    beforeCancel(duration) {
        // 現在処理なし。
    }
    afterCancel(duration) {
        // 現在処理なし。
    }
    getLayerBuilder() {
        return this.session.addMoment().putLayerMoment(this.layerId);
    }
    getEditBuilder() {
        return this.session.addMoment().putLayerMoment(this.editLayerId);
    }
}



