import * as React from "react";
import * as styles from "./TextToolButtonStyle.css";
export class TextToolButton extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        let editorProp = this.props.editorProperties;
        let color = editorProp.color;
        let style = {};
        if (this.props.selected) {
            style.color = `rgb(${color.r},${color.g},${color.b})`;
            style.fontSize = editorProp.fontSize;
            style.fontWeight = editorProp.fontWeight;
        }
        return (React.createElement("div", {className: styles.item}, 
            React.createElement("div", {onClick: () => { this.props.onSelect(); }, className: this.props.selected ? styles.item__circle_selected : styles.item__circle}, 
                React.createElement("div", {className: styles.item__circle_cell}, 
                    React.createElement("span", {style: style, className: "material-icons"}, "text_fields")
                )
            )
        ));
    }
}



