import { HistorySession } from "./HistorySession";
export class SessionQueue {
    constructor(prop) {
        this.prop = prop;
        this.first = null;
        this.last = null;
    }
    enqueue(success, reject) {
        let node = new QueueItem(new HistorySession(this, this.prop), success, reject != null ? reject : success);
        if (this.first == null) {
            this.first = this.last = node;
            node.value.alive = true;
            node.success(node.value);
            return;
        }
        this.last.next = node;
        this.last = node;
    }
    ;
    dequeue() {
        if (this.first == null) {
            return;
        }
        this.first.value.alive = false;
        let node = this.first.next;
        this.first.next = null;
        this.first = node;
        if (node == null) {
            this.last = null;
            return;
        }
        node.value.alive = true;
        node.success(node.value);
    }
    ;
    clearAll() {
        let current = this.first;
        let previous;
        while (current != null) {
            this.first.value.alive = false;
            previous = current;
            current = current.next;
            previous.next = null;
        }
        this.first = null;
        this.last = null;
    }
    ;
}
class QueueItem {
    constructor(session, success, reject) {
        this.value = session;
        this.next = null;
        this.success = success;
        this.reject = reject;
    }
}



