import { PathDrawer } from "./PathDrawer";
export class AbstractModeStroke {
    constructor(viewer, tran, prop) {
        this.viewer = viewer;
        this.tran = tran;
        this.tran.setSavePoint();
        this.pathDrawer = new PathDrawer(tran, prop);
        this.lPointX = -100;
        this.lPointY = -100;
    }
    touchStart(x, y) {
        if (!this.tran.isAlive()) {
            return;
        }
        this.checkLastAccess();
        this.tran.setSavePoint();
        this.pathDrawer.clear();
        this.doStroke(x, y);
        this.started = true;
    }
    touchMove(x, y) {
        if (!this.tran.isAlive()) {
            return;
        }
        this.checkLastAccess();
        let latest = this.time;
        this.time = new Date().getTime();
        if ((this.time - latest) >= 50) {
            this.doStroke(x, y);
            return;
        }
        let x1 = x - this.lPointX;
        let y1 = y - this.lPointY;
        let d = Math.sqrt(x1 * x1 + y1 * y1);
        if (d < 50) {
            this.time = latest;
            this.wPointX = x;
            this.wPointY = y;
            this.setWait();
            return;
        }
        this.doStroke(x, y);
    }
    touchEnd(x, y) {
        if (!this.started) {
            return;
        }
        this.started = false;
        if (!this.tran.isAlive()) {
            return;
        }
        this.checkLastAccess();
        if (this.lPointX === x && this.lPointY === y) {
            this.setCommitProperty(this.tran);
            this.tran.commit(true);
            this.pathDrawer.clear();
            return;
        }
        this.time = new Date().getTime();
        this.doStroke(x, y);
        this.setCommitProperty(this.tran);
        this.tran.commit(true);
        this.pathDrawer.clear();
    }
    checkLastAccess() {
        if (this.commitReserve) {
            this.reset = true;
            return;
        }
        this.commitReserve = true;
        setTimeout(() => {
            if (this.reset) {
                this.reset = false;
                this.checkLastAccess();
                return;
            }
            this.commitReserve = false;
            this.setCommitProperty(this.tran);
            this.tran.commit(true);
            this.pathDrawer.clear();
        }, 1000);
    }
    setWait() {
        if (this.waiting) {
            return;
        }
        this.waiting = true;
        setTimeout(() => {
            if (!this.waiting) {
                return;
            }
            this.waiting = false;
            if (!this.tran.isAlive()) {
                return;
            }
            let now = new Date().getTime();
            if (now - this.time < 100) {
                this.setWait();
                return;
            }
            this.doStroke(this.wPointX, this.wPointY);
        }, 100);
    }
    doStroke(x, y) {
        this.viewer.stop();
        try {
            this.waiting = false;
            this.lPointX = x;
            this.lPointY = y;
            this.tran.restoreSavePoint();
            this.setProperty(this.tran);
            this.pathDrawer.push(this.lPointX, this.lPointY).doPlot(false, false);
        }
        finally {
            this.tran.flush();
            this.viewer.start();
        }
    }
    setText(text) {
        // 処理なし。
    }
    backward() {
        // 処理なし。
    }
}



